import React, { useState } from "react";
import { reactify } from "svelte-preprocess-react";
import Child from "./Child.svelte";

const ReactifiedChild = reactify(Child);

export default () => {
    let [state, setState] = useState(0);
    return (<>
        <ReactifiedChild name="Jibo" click={() => setState(state + 1)} value={state} />
        <p>React says you've clicked {state} times.</p>
    </>)
}